<template>
  <div id="main">
    <div class="login-form">
      <div class="clear"></div>
      <div class="avtar">
        <img src="../assets/logo.png"/>
      </div>
      <form>
        <input type="text" class="text" v-model="username">
        <div class="key">
          <input type="password" v-model="password">
        </div>
      </form>
      <div class="signin">
        <input type="submit" value="Login" @click="login">
      </div>
    </div>
  </div>
</template>

<script>
  import {http} from "../util/service";

  export default {
    name: 'Login',
    data() {
      return {
        username: '',
        password: ''
      }
    },
    methods: {
      async login() {
        let url = "/login";
        let param = {};
        param.username = this.username;
        param.password = this.password;

        let result = await http.post(url, param);
        let data = result.data;
        console.log(data);
        if (data.success) {
          sessionStorage.setItem('webToken', data.data);
          this.$router.push({path: '/home'});
        } else {
          this.$message({
            message: '账号密码错误',
            type: 'error'
          });
          this.username = '';
          this.password = '';
        }
      }
    }

  }
</script>

